import React from 'react';
import { Table } from 'reactstrap';

const TimeComplexityTable = ({ access, search, insertion, deletion }) => {
    return (
        <Table dark className="table-sm text-center">
            <thead>
                <tr>
                    <th>Access</th>
                    <th>Search</th>
                    <th>Insertion</th>
                    <th>Deletion</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{access}</td>
                    <td>{search}</td>
                    <td>{insertion}</td>
                    <td>{deletion}</td>

                </tr>
            </tbody>
        </Table>
    );
}

export default TimeComplexityTable;


