import React from 'react'
import TimeComplexityTable from '../../components/table/Complexity';
import SubjectCard from '../../components/card/Subject';
import { Alert } from 'reactstrap';


const TimeComplexity = props => {
    return (
        <SubjectCard title="Stack Time Complexity">
            <TimeComplexityTable
                access="O(n)"
                search="O(n)"
                insertion="O(1)"
                deletion="O(1)"
            />
            <Alert color="warning" >
                <p>Items are not deleted form the stack.
                    the value of the top item in the stack is reassigned.
                </p>
                <p>If the element in the stack is deleted then the when using an array
                    then the deletion complexity would be "O(n)".
                </p>
            </Alert>
        </SubjectCard>
    )
}



export default TimeComplexity

