import React from 'react'
import PropTypes from 'prop-types'
import { DropdownItem } from 'reactstrap';

const MapDropdownLinks = (props) => {
    const sLinks = props.links.map((item) =>
        <DropdownItem key={item.name} href={item.link}>{item.name}</DropdownItem>
    )
    return sLinks
}

MapDropdownLinks.propTypes = {
    links: PropTypes.array.isRequired
}

export default React.memo(MapDropdownLinks);
