import React from 'react'
// import PropTypes from 'prop-types'
import { UncontrolledButtonDropdown, DropdownMenu, DropdownToggle } from 'reactstrap';
import MapDropdownLinks from './mapDropdownLinks';
import linkStructure from '../../../helpers/links/structures.json'


function DataStructures() {
    return (
        <UncontrolledButtonDropdown>
            <DropdownToggle caret size="sm">
                Data
          </DropdownToggle>
            <DropdownMenu>
                <MapDropdownLinks links={linkStructure} />
            </DropdownMenu>
        </UncontrolledButtonDropdown>
    );
}

// Sorting.propTypes = {

// }

export default DataStructures